.navegacion{
    //opacity: 0;
    position:absolute;    
    position:fixed;    
    right:0;top:5.5rem;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 15% 100%);
    padding-top:0;
    display: flex;
    flex-direction: column;
    white-space: nowrap;
  overflow: hidden;
    background-color: $cafeOscuro;
    width: 0;
    height: 25rem;
    text-align: right;
    
    a {
        position:relative;
        font-size:2.5rem;
        text-align: center;
        font-weight: bold;
        color:$colorSubTitulos;
        text-shadow: 1px 1px 1px $negro;

        &:after{            
            border-radius: 1rem;
            border-top: .8rem solid $colorTitulos;
            content:"";
            position: absolute;            
            right: 100%;
            left: 0;
            bottom: .14rem;
            transition: right .25s .1s cubic-bezier(0,.5,0,1);

        }

        &:hover{
            color:$colorTitulos;
            font-size: 2.7rem;
            &:after{
                right:0;
            }
        }

        transition: all .3s .25s cubic-bezier(0,.5,0,1);

    }
    transition: all .5s cubic-bezier(0,.88,.63,.99);
    z-index: 40;
}

.navegacion.is-visible {
    //opacity:1;
    width:25rem;
}

.hamburguesa {
    background-color: $cafeOscuro;
    opacity:0;
    position:absolute;
    top: 5%;
    right:3%;
    @include telefono {
        right:0;
    }
    overflow-x: hidden;
    clip-path: polygon(9% 0, 100% 0%, 100% 100%, 9% 100%, 0% 50%);
    //box-shadow: .1rem .1rem .2rem rgb(109, 72, 4);
    width:0%;
    svg {
        width: 6.5rem;
    }
    transition: all .3s .1s cubic-bezier(0,.95,.59,1);
    z-index: 45;
    &:hover{
        transform: scale(1.1);
    }
}

.hamburguesa.is-visible{
    opacity:1;
    position: fixed;
    width:8rem;    
    overflow-x: hidden;
    
}



