.animacion {
    
    overflow: hidden;
    position:relative;width:100vw;height:30rem;
    left: calc(-50vw + 50%);

    .fondo{
        background: linear-gradient(90deg, rgba(15, 84, 211, 0.375) 0%, rgba(144, 248, 248, 0.125) 100%);
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        animation-name: fondo;
        animation-duration: 60s;
        animation-iteration-count: infinite;
    }

    .sun{
        background-color: rgb(199, 154, 6);
        position: absolute;
        left: -70px;
        top: 50%;
        width: 60px;
        height: 60px;
        border-radius: 100px;
        box-shadow: 0 0 10px rgb(133, 103, 5);
        animation-name: sun;
        animation-duration: 60s;
        animation-iteration-count: infinite;
    }

    .rig{
        background-image: url(..//img/foothills.webp);
        position: absolute;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: bottom;
        top: 0%;
        width: 100%;
        height: 100%;
    }

    .nubes{
        background-image: url(..//img/nubes.webp);
        position: absolute;
        background-repeat: repeat-x;
        width: 100%;
        height: 80px;
        background-size: contain;
        animation: nubes 40s linear infinite; 
    }

    .marquee-quote{
        position:absolute;
        left:50%;
        top:5%;
        p{color:$cafeOscuro; 
            font-size:1.9rem;
            font-family: $fuente_titulos;
            text-shadow: .1rem .1rem .2rem $amarillo;
            @include tablet{font-size:2.3rem;}
            @include desktop{font-size:2.8rem;}
        span{font-weight:600;
            font-size:2rem;}}
    }
}
