.menu-admin{
    display:block;
    flex-direction: column;
    align-items: center;
    text-align: center;
    h2{color:$grisOscuro;}
}

.goback{
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    justify-content: center;
    width:100%;
}

.boton {
    position:relative;
    width:50%;
    @include tablet{width:60%;};
    font-family: $fuente_principal;
    font-size:2.2rem;
    font-weight:600;
    background-color: $colorSubTitulos;
    border:.1rem $cafeOscuro;
    box-shadow: .3rem .3rem 0px 0px $cafeOscuro;
    color: $cafeOscuro;
    //padding: 16px 32px;
    text-decoration: none;
    cursor: pointer;
    transition: all .15s .1s ease-out;
    &:hover{
        transform: scale(1.1);
    }
    margin-bottom:3rem;
    margin-top:3rem;

    &.red-botton{
        background-color: $rojo;
    }

  }