.no-webp .contenedor-header:before {    
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -2;
    background: linear-gradient(90deg, rgba(0,0,0, 0), rgba(0,0,0,0.6)),
                url(../img/guitar.jpg) no-repeat center;
                background-size: cover;
}

.webp .contenedor-header:before {    
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -2;
    background: linear-gradient(90deg, rgba(0,0,0, 0), rgba(0,0,0,0.6)),
                url(../img/guitar.webp) no-repeat center;
                background-size: cover;
}
    

.no-webp .main-header:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;    
    
    z-index:1;
    animation: change-bck 3s cubic-bezier(.44,0,.25,.98) forwards;
    overflow: hidden;
    background: linear-gradient(to top, rgba(0,0,0,0), rgba(0,0,0,0.3)),
                url(../../build/img/guitar1.png) no-repeat center;
                background-size: cover;
}

.webp .main-header:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;    
    
    z-index:1;
    animation: change-bck 3s cubic-bezier(.44,0,.25,.98) forwards;
    overflow: hidden;
    background: linear-gradient(to top, rgba(0,0,0,0), rgba(0,0,0,0.3)),
                url(../../build/img/guitar1.webp) no-repeat center;
                background-size: cover;
}



.contenedor-header {
    align-items: center;
    flex-direction: column;
    display: flex;
    grid-template-columns: repeat(2, 1fr);
    height: 100vh;
    justify-content: start;
    overflow: hidden;
    position: relative;
    transform-style: preserve-3d;
    perspective: 100px;
    
 
}

.contenedor-header p{
    text-shadow: 1px 1px 3px #000;
}



.main-header p {
    margin: 0;
    animation: pop-in 1s 2.3s cubic-bezier(0,.92,.31,1.18) forwards;
    &.generic{
        animation: pop-in .25s 0.35s cubic-bezier(0,.92,.31,1.18) forwards;
    }
    opacity: 0;
    color: $colorSubTitulos;
    
}


.contenedor-header:after {
    background: $colorFondosClaros;
    content: "";
    height: 40rem;
    left: -5%;
    position: absolute;
        right: -5%;
        top: 90%;
            transform-origin: 0 0;
    opacity:0;
    animation: rotate-up 1s 2.0s cubic-bezier(0,.64,.29,1.07) forwards;
    z-index:3;
}

.header-title{
    margin-bottom:1rem;
    animation: pop-in 1s 2s cubic-bezier(0,.92,.31,1.18) forwards;
    &.generic{
        animation: pop-in .25s 0.1s cubic-bezier(0,.92,.31,1.18) forwards;
    }
    opacity: 0;
}
.header-title, .header-subtitle {    
    grid-column:1;
    
    text-shadow: 1px 1px 5px #000;
}

.header-subtitle {
    margin-bottom: 3rem;
    margin-top: 1rem;
    font-size: 4.2rem;
    font-weight: 500;
    animation: pop-in 1s 2.2s cubic-bezier(0,.92,.31,1.18) forwards;
    &.generic{
        animation: pop-in .25s 0.2s cubic-bezier(0,.92,.31,1.18) forwards;
    }
    opacity: 0;
    color:$colorSubTitulos;
}

.icon-tabler-map-pin {
    animation: pop-in 1s 2.3s cubic-bezier(0,.92,.31,1.18) forwards;
    opacity: 0;
}

.header-button {
    position: relative;
            transform: translateZ(.1px);
    z-index: 10;
}

.header-down-arrow {
    display: flex;
    position: absolute;
    justify-content: center;
    bottom: 4vh;
    left:0;
    right:0;
    text-align: center;
    z-index:12;
    
}

.header-down-arrow img{
    opacity: 0;
    animation: fade-slide-up 4s 3.5s ease-out infinite;
    width:50px;
}


/****************************************************************************/
/****************************************************************************/
/*ANIMACIONES*/
/****************************************************************************/
/****************************************************************************/

// .js-loading *,
// .js-loading *:before,
// .js-loading *:after {
//   animation-play-state: paused !important;
// }


@keyframes change-bck {
    0% {
      opacity: 1;
      transform: scale(1.5);
    }

    50% {
        opacity: .9;
        transform: scale(1.1);
      }

    100% {        
      opacity: 0;
      transform: none;
    }
}   

@keyframes fade-slide-down {
    0% {
      opacity: 0;
      transform: translateY(-4rem);
    }
    100% {        
        opacity: 1;
        transform: none;
    }
}


@keyframes fade-slide-up {
    0% {
      opacity: 0;
      transform: translateY(-1rem);
    }
    100% {        
        opacity: 1;
        transform: none;
    }
}

@keyframes pop-in {
    0% {
      opacity: 0;
      transform: translateY(-4rem) scale(.8);
    }
    100% {        
        opacity: 1;
        transform: none;
    }

}

@keyframes rotate-up {

    100% {        
        transform: rotateZ(-4deg);
        opacity:1;
    }
}
