/**********************************************************************************************************/
/*AboutMe Section*/
.about-me{
    position:relative;
    text-align:center;
    .boton-mas{
        margin-top:5rem;
        margin-bottom:10rem;
    }
}

.about-me::after{
    content: "";
    position:absolute;
    left:0;
    bottom:0rem;
    width:100%;
    background-color: $colorTitulos;
    height:.6rem;
}

.no-webp .about-me:before{
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -2;
    background-color: $colorFondosClaros;
    // background: linear-gradient(to top, rgba(0,0,0,0.4), rgba(0,0,0,1)),
    //             url(../img/fondo2.jpg) no-repeat center;
}

.webp .about-me:before{
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -2;
    background-color: $colorFondosClaros;
    // background: linear-gradient(to top, rgba(0,0,0,0.4), rgba(0,0,0,1)),
    //             url(../img/fondo2.webp) no-repeat center;
    //             background-size: cover;
}

.titulo-about-me,
.titulo-research,
.titulo-blog,
.titulo-contacto
{
    padding-top:3rem;
    margin-top: 0;
    opacity:0; 
    transform: translateY(-4rem) scale(.7);
    transition: transform 1s .25s cubic-bezier(0,1,.3,1),
                opacity .3s .25s ease-out;
    will-change: transform, opacity;

}
.titulo-about-me.is-visible,
.titulo-research.is-visible,
.titulo-blog.is-visible,
.titulo-contacto.is-visible{
    opacity:1;
    transform: none;
}

.contenedor-about-me{
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    row-gap: 4rem;
    div {
        border-radius: 1rem;
        border: .6rem solid $colorTitulos;
        box-shadow: .1rem .1rem .2rem $colorSombraContainers;
        gap:10rem;
        background: linear-gradient(to bottom, rgba(245, 233, 167, 0.4), rgba(255,255,255,0.6));

    }
    @include tablet{
        grid-template-columns: repeat(2, 1fr);
    }

    &:last-of-type{
        display:flex;
        flex-direction: column;
        align-items: center;
        row-gap: 0;
        margin-bottom:5rem;
    }
   
}

.about-me-photo{min-width:30rem;max-width: 50rem;
    position:relative;
    &::after{
        content:"";
        position:absolute;
        bottom:0;
        left:0;
        width:100%;
        height:1rem;
        background-color: $colorTitulos;
        opacity:1;
        @include tablet{opacity:0;}
    }

}
.seccion-about-me {
    margin-right:1.5rem;
    margin-left:1.5rem;
    margin-bottom: 1.5rem;
    display: block;
}

.about-me-intro{
    display:flex;
    flex-direction: column;
    @include tablet{flex-direction: row;}
}

.about-me-intro p{
    margin-right:1.5rem;
    margin-left:1.5rem;
    margin-bottom:6rem;
    font-size:2.5rem;
    @include desktop{font-size:3.2rem;}
    span{font-weight:600;
        font-size: 4rem;
        }
}

.lista {
    margin-right:1.5rem;
    text-align: left;
    margin-bottom:5rem;
    li{
        margin-bottom:2rem;
        list-style-type: none;
        line-height: 3rem;
        font-size: 2.5rem;
        span{
            color:$cafeAcento;
            font-weight: 600;
            font-size: 2.5rem;
        }
    }
}

.about-me-education,
.about-me-experience,
.about-me-awards,
.about-me-publications{position:relative;}
.about-me-education::after{
    content:"";
    background:url(../img/grad.svg);
    background-size: cover;
    width:8rem;
    height:8rem;
    z-index:30;
    position:absolute;    
    top:-5rem;
    right:3rem;
}

.about-me-experience::after{
    content:"";
    background:url(../img/tie.svg);
    background-size: cover;
    width:8rem;
    height:8rem;
    z-index:30;
    position:absolute;    
    top:-5rem;
    right:3rem;
}

.about-me-awards::after{
    content:"";
    background:url(../img/medal.svg);
    background-size: cover;
    width:8rem;
    height:8rem;
    z-index:30;
    position:absolute;    
    top:-5rem;
    right:3rem;
}

.about-me-publications::after{
    content:"";
