.contact{
    h2{font-size:3rem;}
}
.contenedor-form{
    overflow:hidden; 
    display:block;
    h2{color:$grisOscuro;}}

.formulario{
    margin-right:4rem;
    margin-left:4rem;
    label{
        font-weight: 600;
        font-size: 2.5rem;
        font-family: $fuente_principal;
        display:block;
    }

    input:not([type="submit"]),
    textarea, select{
        box-sizing: border-box;
        margin-bottom:2rem;
        padding:1rem;
        font-size: 2.2rem;
        font-family: $fuente_principal;
        display:block;
        width:100%;        
        background-color: $blanco;
        border: 1px solid $colorTitulos;
        border-radius:1rem;

        &[type="radio"] {width:10rem; margin-bottom:0;}
    }

    textarea{
        height:20rem;
    }

    .radio-current {
        display: flex;
        width: 100%;
        text-align: left;
    }
    
}

