.container-blog{
    display:grid;
    grid-template-columns: 1fr;
    gap:2rem;
    h2{font-size:3.0rem;
        margin-bottom:0;
        margin-left:1rem;margin-right:1rem;
        color:$grisOscuro;}
}

.blog-entry{
    overflow:hidden;
    p {font-size:2.5rem;
        line-height:2.8rem;
        text-align:left;
        margin-top:0;}
    span {
        font-family: $fuente_subTitulos;
        font-size:2.5rem;
        line-height:2.1rem;
    }
    
    .boton-mas{

        @include desktop{float:right;margin-right:5rem;}
        
    }

}

.entry{margin-left:3rem;
margin-right:3rem;}

.contenido-entry{
    display:grid;
    grid-template-columns: 1fr;
    gap: 2rem;
    margin-bottom:1.5rem;
    @include desktop{grid-template-columns: 1fr 2fr;}
}

.contenedor-boton{
    display:flex;
    flex-direction:column;
    align-items: center;
    
}