
.marquee{width:100vw !important;position:relative;left: calc(-50vw + 50%);}

.marquee-image{
    height:30rem; 
    width:100%;
    margin-bottom:10rem;
    
    &.first-marquee{
        background: linear-gradient(to bottom, rgba(0,0,0,0.3), rgba(0,0,0,1)),
                url(..//img/fondo2.webp);
        background-position: 0% 65%;
        background-size: cover;
    }

    &.second-marquee{
        background: linear-gradient(to bottom, rgba(0,0,0,0.3), rgba(0,0,0,1)),
                url(..//img/fondo4.webp);
        background-position: 0% 65%;
        background-size: cover;
    }

    .marquee-text{
        opacity:0;
        transform: translateY(-2rem) scale(.9);
        transition: all .3s .1s ease-out;
        &.is-visible{opacity:1;transform:none;}
    }

    text-align: center;
    overflow:visible;
    position:relative;

    p{
        font-family:$fuente_titulos;
        margin-left:8rem;
        float:left;
        color:$cafeAcento;
        font-size:2.0rem;
        @include telefono{font-size:2.8rem};
    span{font-weight:600}}

    &::after{
        content:"";
        opacity:0;
        @include telefono{opacity:1;}
        width:40rem;
        height:40rem;
        position:absolute;
        right:-6rem;
        z-index:30;
        
    }

    &.first-marquee{p{font-size:1.7rem; @include telefono{font-size:2.5rem};}}
    &.first-marquee::after{
        background-image: url(..//img/sanger.webp);
        background-size: cover;
        background-repeat: no-repeat;        
        opacity:0;
        @include telefono{opacity:1;}
    }

    &.second-marquee::after{
        background-image: url(..//img/seneca.webp);
        background-size: contain;
        background-repeat: no-repeat;        
        opacity:0;
        @include telefono{opacity:1;}
    }

    img{position:absolute;width:10rem;top:1rem;left:1rem;z-index:1;}
}