.contenedor-header-generico {
    align-items: center;
    flex-direction: column;
    display: flex;
    grid-template-columns: repeat(2, 1fr);
    height: 30vh;
    justify-content: start;
    overflow: hidden;
    position: relative;
    //transform-style: preserve-3d;
    //perspective: 100px;
    
 
}

.contenedor-header-generico {    
    &::before{
        content: "";
        position: absolute;
        top: 0;    left: 0;
        right: 0; bottom:0;
        z-index: -1;
        background: linear-gradient(90deg, rgba(0,0,0, 0), rgba(0,0,0,0.6)),
                    url(../../build/img/guitar.webp) no-repeat center;
                    background-size: cover;
    }



}

