html {
    font-size: 62.5%;
    box-sizing: border-box;
    overflow-x: hidden;
}
*, *:before, *:after {
    box-sizing: inherit;
}

body {
    font-family: $fuente_principal;
    line-height: 1.8;
    overflow-x: hidden;
    // background:linear-gradient(to bottom, rgba(0,0,0,0.5), rgba(0,0,0,1)),
    //             url(..//img/wallpaper.webp);
    //             background-size:cover;
    background-color:$colorFondosClaros;
    width: 100%;
}
p {
    color: $grisOscuro;
    font-size: 3rem;
}
li{
    font-size: 2.5rem;
    color: $grisOscuro;
}
main{max-width: 114rem;
margin: 0 auto;overflow-x: hidden;}


.contenedor {
    max-width: 90%;
    margin: 0 auto;
    overflow: hidden;
}

a {
    text-decoration: none;
    &:hover{
        cursor: pointer;
    }
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    margin: calc($separacion / 2) 0;
    font-weight: 400;
    text-align: center;
    
}

h1 {
    font-size: 6rem;
    @include tablet{font-size: 8.8rem;};
    font-family: $fuente_titulos;
    text-shadow: 1px 1px 1px $negro;
    color: $colorTitulos;
}
h2 {
    font-size: 3.5rem;
    @include tablet{font-size: 4.2rem;};
    font-family: $fuente_titulos;
    color: $colorTitulos;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}

input[type=submit] {
    width:50%;
    @include tablet{width:40%;};
    font-family: $fuente_principal;
    font-size:2.2rem;
    font-weight:600;
    background-color: $colorSubTitulos;
    border:.1rem $cafeOscuro;
    box-shadow: .3rem .3rem 0px 0px $cafeOscuro;
    color: $cafeOscuro;
    //padding: 16px 32px;
    text-decoration: none;
    margin: 4px 2px;
    cursor: pointer;
    transition: all .15s .1s ease-out;
    &:hover{
        transform: scale(1.1);
    }
    margin-bottom:2rem;
  }


.enclosed-box{
    
    border-radius: 1rem;
    border: .6rem solid $cafeAcento;
    box-shadow: .1rem .1rem .2rem $colorSombraContainers;
    gap:10rem;
    background: linear-gradient(to bottom, rgba(136, 123, 3, 0.5), rgba(255,255,255,0.6));
    margin-bottom:2rem;
    display:block;
    align-items:center;
    text-align: center;
    &:last-of-type{margin-bottom:8rem;}
    h2{margin-left:3rem;
        margin-right:3rem;}

}


header {
    overflow:hidden;
}

