footer {
    background-color: $cafeAcento;
    width:100%;
    padding-bottom: calc( $separacion/3 );
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .contenedor-footer  {
        width:100%;

    };

    .copyright{
        text-align: center;
        p{color: $grisOscuro;
        font-size: 1.8rem;}
    }
}

.iconos-nosotros {
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content:center;

    a {margin-right: 1rem; margin-left:1rem;
        
        img {
        max-height: 6rem;
        transition: all .3s .1s ease-out;
        &:hover{max-height:6.1rem}}
    }
}

.iconos-nosotros-footer {
    a {margin-top: 2rem;}
    a img {max-height: 4rem;}
}
