
.research-projects-container{
    h2{font-size:3.0rem;
        margin-bottom:0;    
        color:$grisOscuro;}
    margin-right:2rem;
    margin-left:2rem;
    margin-bottom:5rem;
    display:flex;flex-direction:column;
    position:relative;
}

.research-projects-container::after{    
    content: "";
    position:absolute;
    left:0;
    bottom:2rem;
    width:100%;
    background-color: $colorTitulos;
    height:.6rem;
    margin-top:5rem;
}

.research-project{
    
    border-radius: 1rem;
    border: .6rem solid $cafeAcento;
    box-shadow: .1rem .1rem .2rem $colorSombraContainers;
    gap:10rem;
    background: linear-gradient(to bottom, rgba(136, 123, 3, 0.5), rgba(255,255,255,0.6));
    margin-bottom:4.5rem;
    display:block;
    align-items:center;
    text-align: center;
    position:relative;
    &:last-of-type{margin-bottom:8rem;}
    //max-height:55rem;
    //@include telefono{height:33rem;}
    //&.is-expanded{max-height:300rem;height:auto;}
    //overflow:hidden;
    h2{margin-left:3rem;
        margin-right:3rem;}

}

.contenido-project{display: flex;}
.contenido-project,
.contenido-expandable-box{
    position:relative;
    margin-right:2.5rem;
    margin-left:2rem;
    margin-top:10rem;
    margin-bottom:5rem;
    flex-direction: column;
    @include desktop{flex-direction: row; align-items: center;}
    justify-content: space-between;
    align-items: center;
    visibility: visible;
    max-height:0;
    overflow: hidden;
    &.is-expanded{max-height:500rem;};    
    transition:max-height 0.5s cubic-bezier(.51,.34,.45,.82);


    p {font-size:2.5rem;
    line-height:2.8rem;
    float:right;
    text-align:left;
    margin-left:2rem;}
    span {
        font-family: $fuente_subTitulos;
        font-size:2.5rem;
        line-height:2.1rem;
    }
    picture{
        float:left;    
        max-height:80rem;
        @include desktop{max-height:40rem;}
        max-width:30rem;
        min-width: 18rem;
        min-height: 31rem;
        margin-left:2rem;
        margin-right:3rem;
    }

}

.headline-project,
.headline-expandable-box{
    h3{font-size:2.5rem; 
        margin-left:3rem; 
        margin-right:3rem;
        @include desktop{font-size:3rem;}}
    .expand-info{
        width:5rem;
        height:5rem;
        float:right;
        margin-right:2rem;
        background: url(..//img/minus.svg);
        &.mas{
        background: url(..//img/plus.svg);
        }
        transition:all .25s ease-out;
        &:hover{transform:scale(1.2)}
    }
}


.research-project::after{
    content:"";
    background:url(../img/flask.svg);
    background-size: cover;
    width:8rem;
    height:8rem;
    z-index:30;
    position:absolute;    
    top:-4rem;
    right:3rem;
}