.blog-quotes{overflow:hidden;position:relative;
    h2{color:$grisOscuro;}}
.quotes-container{
    position:relative;
    width:100%;
    margin: 0 auto;
}

.quotes{
    position:relative;    
    width:100%;
    height: 70rem;
    padding:0;
    p{font-size:2.5rem;
    span{font-weight: 600;};}
}

.quote{
    cursor: pointer;
    margin: 2em auto;
    opacity:0;
    list-style: none;
    padding:.5rem;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(calc(100% + 12em), -50%) scale(.6);
    transition: transform 1.5s cubic-bezier(0.7,-.3,.2,1.3),
                opacity .5s .6s ease-out;
    width:30rem;

    

    &.previous{
        opacity: .5;
        transform: translate(-150%, -50%) scale(.8);
    }

    &.current{
        cursor: default;
        opacity: 1;
        transform: translate(-50%, -50%);
    }

    &.next{
        opacity: .5;
        transform: translate(50%, -50%) scale(.8);
    }

    &.left{
        transform: translate(calc(-150% - 12em), -50%) scale(.6);
    }
}

.quote-image  {
    background-size: cover;
    filter: grayscale(100%);
    box-shadow: 0 .2rem .4rem rgba(0,0,0,.6);
    height:20rem;   
    border-radius: 6rem; 
    position: absolute;
    top: -8em;
    left:calc(50% - 3.5em);
    width:20rem;    
    
}

.quote-text img{
    width:6rem;
    position:absolute;
    top:-2.8rem; 
    left:-2rem;
    opacity:0; 
    transition: opacity .5s .3s ease-out;
    &.visible{opacity:1;}}

.quotes-pips{
    position:absolute;
    bottom:3rem;
    text-align:center;
    width:100%;

    ul{
        list-style:none;
        margin:0; padding:0;
        width:100%;

    }
}

.pip{
    background-color:$cafeAcento;
    border-radius: 50%;
    border: .2rem solid $amarillo;
    cursor: pointer;
    display: inline-block;
    height: 2rem;
    margin: 0 .1rem;
    padding: 0;
    transform: scale(.5);
    transition: all .8s cubic-bezier(0,1,.3,1);
    width: 2rem;

    &.previous,
    &.next{background-color: $colorTitulos;
            transform: scale(.6);}
    &.current{background-color: $blanco;
        cursor:default;
        transform: scale(.8);}
    
    &:not(.current):hover{
        transform: scale(1);
    }
}

