.alert{
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: bold;
    text-transform: uppercase;
    margin: 1rem 0;
    font-size:1.8rem;

    &.errormsg{
        background-color: $rojo;
    }

    &.okmsg{
        background-color: $verde;
    }
}

.admin-panel{
    display:flex;
    flex-direction: column;
    align-items: center;
}

table.table-projects{
    margin-left: 2rem; margin-right: 2rem;
    padding-left: 1.5rem;padding-right: 1.5rem;
    width: 100%;
    border-spacing: 0.2rem;
    thead{
        background-color:$grisOscuro;
        th{
            color: $blanco;
            font-size: 1.8rem;
            padding-left:0.5rem;
            padding-right:0.5rem;
            text-transform: uppercase;
        }
    }

    .icons-adminpanel{
        svg{
            max-height: 5rem;
            &:hover{transform: scale(1.2);}            
            transition: transform .2s 0.1s ease-in-out;
        }
    }

    .image-table{height:10rem;width:12rem}

    td{
        font-size: 1.5rem;
    }
}

.bar {
    height: .7rem;
    width: 90%;
    margin: 2rem 0 2rem 0;
    background-color:$grisOscuro;
}

.imagen-small{
    width:20rem;
}

.article-preview {
    height: 30rem;
    max-height: 30rem;
    overflow: hidden;
    font-size: 1.8rem;    
    border: 1px solid $colorTitulos;
    resize: none;
    border-radius:1rem;
    background-color: rgb(240, 240, 235);
}