/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  overflow-x: hidden;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Random Grotesque Slim Book", sans-serif;
  line-height: 1.8;
  overflow-x: hidden;
  background-color: #f6efdc;
  width: 100%;
}

p {
  color: #333333;
  font-size: 3rem;
}

li {
  font-size: 2.5rem;
  color: #333333;
}

main {
  max-width: 114rem;
  margin: 0 auto;
  overflow-x: hidden;
}

.contenedor {
  max-width: 90%;
  margin: 0 auto;
  overflow: hidden;
}

a {
  text-decoration: none;
}
a:hover {
  cursor: pointer;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 2.5rem 0;
  font-weight: 400;
  text-align: center;
}

h1 {
  font-size: 6rem;
  font-family: "Eboracum", sans-serif;
  text-shadow: 1px 1px 1px #000000;
  color: #ccb42a;
}
@media (min-width: 768px) {
  h1 {
    font-size: 8.8rem;
  }
}

h2 {
  font-size: 3.5rem;
  font-family: "Eboracum", sans-serif;
  color: #ccb42a;
}
@media (min-width: 768px) {
  h2 {
    font-size: 4.2rem;
  }
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

input[type=submit] {
  width: 50%;
  font-family: "Random Grotesque Slim Book", sans-serif;
  font-size: 2.2rem;
  font-weight: 600;
  background-color: #d6c97c;
  border: 0.1rem rgb(29, 14, 3);
  box-shadow: 0.3rem 0.3rem 0px 0px rgb(29, 14, 3);
  color: rgb(29, 14, 3);
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
  transition: all 0.15s 0.1s ease-out;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  input[type=submit] {
    width: 40%;
  }
}
input[type=submit]:hover {
  transform: scale(1.1);
}

.enclosed-box {
  border-radius: 1rem;
  border: 0.6rem solid rgb(207, 173, 80);
  box-shadow: 0.1rem 0.1rem 0.2rem #b4b2ab;
  gap: 10rem;
  background: linear-gradient(to bottom, rgba(136, 123, 3, 0.5), rgba(255, 255, 255, 0.6));
  margin-bottom: 2rem;
  display: block;
  align-items: center;
  text-align: center;
}
.enclosed-box:last-of-type {
  margin-bottom: 8rem;
}
.enclosed-box h2 {
  margin-left: 3rem;
  margin-right: 3rem;
}

header {
  overflow: hidden;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/*UI*/
.no-webp .contenedor-header:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -2;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6)), url(../img/guitar.jpg) no-repeat center;
  background-size: cover;
}

.webp .contenedor-header:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -2;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6)), url(../img/guitar.webp) no-repeat center;
  background-size: cover;
}

.no-webp .main-header:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  animation: change-bck 3s cubic-bezier(0.44, 0, 0.25, 0.98) forwards;
  overflow: hidden;
  background: linear-gradient(to top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3)), url(../../build/img/guitar1.png) no-repeat center;
  background-size: cover;
}

.webp .main-header:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  animation: change-bck 3s cubic-bezier(0.44, 0, 0.25, 0.98) forwards;
  overflow: hidden;
  background: linear-gradient(to top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3)), url(../../build/img/guitar1.webp) no-repeat center;
  background-size: cover;
}

.contenedor-header {
  align-items: center;
  flex-direction: column;
  display: flex;
  grid-template-columns: repeat(2, 1fr);
  height: 100vh;
  justify-content: start;
  overflow: hidden;
  position: relative;
  transform-style: preserve-3d;
  perspective: 100px;
}

.contenedor-header p {
  text-shadow: 1px 1px 3px #000;
}

.main-header p {
  margin: 0;
  animation: pop-in 1s 2.3s cubic-bezier(0, 0.92, 0.31, 1.18) forwards;
  opacity: 0;
  color: #d6c97c;
}
.main-header p.generic {
  animation: pop-in 0.25s 0.35s cubic-bezier(0, 0.92, 0.31, 1.18) forwards;
}

.contenedor-header:after {
  background: #f6efdc;
  content: "";
  height: 40rem;
  left: -5%;
  position: absolute;
  right: -5%;
  top: 90%;
  transform-origin: 0 0;
  opacity: 0;
  animation: rotate-up 1s 2s cubic-bezier(0, 0.64, 0.29, 1.07) forwards;
  z-index: 3;
}

.header-title {
  margin-bottom: 1rem;
  animation: pop-in 1s 2s cubic-bezier(0, 0.92, 0.31, 1.18) forwards;
  opacity: 0;
}
.header-title.generic {
  animation: pop-in 0.25s 0.1s cubic-bezier(0, 0.92, 0.31, 1.18) forwards;
}

.header-title, .header-subtitle {
  grid-column: 1;
  text-shadow: 1px 1px 5px #000;
}

.header-subtitle {
  margin-bottom: 3rem;
  margin-top: 1rem;
  font-size: 4.2rem;
  font-weight: 500;
  animation: pop-in 1s 2.2s cubic-bezier(0, 0.92, 0.31, 1.18) forwards;
  opacity: 0;
  color: #d6c97c;
}
.header-subtitle.generic {
  animation: pop-in 0.25s 0.2s cubic-bezier(0, 0.92, 0.31, 1.18) forwards;
}

.icon-tabler-map-pin {
  animation: pop-in 1s 2.3s cubic-bezier(0, 0.92, 0.31, 1.18) forwards;
  opacity: 0;
}

.header-button {
  position: relative;
  transform: translateZ(0.1px);
  z-index: 10;
}

.header-down-arrow {
  display: flex;
  position: absolute;
  justify-content: center;
  bottom: 4vh;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 12;
}

.header-down-arrow img {
  opacity: 0;
  animation: fade-slide-up 4s 3.5s ease-out infinite;
  width: 50px;
}

/****************************************************************************/
/****************************************************************************/
/*ANIMACIONES*/
/****************************************************************************/
/****************************************************************************/
@keyframes change-bck {
  0% {
    opacity: 1;
    transform: scale(1.5);
  }
  50% {
    opacity: 0.9;
    transform: scale(1.1);
  }
  100% {
    opacity: 0;
    transform: none;
  }
}
@keyframes fade-slide-down {
  0% {
    opacity: 0;
    transform: translateY(-4rem);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes fade-slide-up {
  0% {
    opacity: 0;
    transform: translateY(-1rem);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes pop-in {
  0% {
    opacity: 0;
    transform: translateY(-4rem) scale(0.8);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes rotate-up {
  100% {
    transform: rotateZ(-4deg);
    opacity: 1;
  }
}
.contenedor-header-generico {
  align-items: center;
  flex-direction: column;
  display: flex;
  grid-template-columns: repeat(2, 1fr);
  height: 30vh;
  justify-content: start;
  overflow: hidden;
  position: relative;
}

.contenedor-header-generico::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6)), url(../../build/img/guitar.webp) no-repeat center;
  background-size: cover;
}

.contenedor-header-generico p {
  text-shadow: 1px 1px 3px #000;
}

footer {
  background-color: rgb(207, 173, 80);
  width: 100%;
  padding-bottom: 1.6666666667rem;
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
footer .contenedor-footer {
  width: 100%;
}
footer .copyright {
  text-align: center;
}
footer .copyright p {
  color: #333333;
  font-size: 1.8rem;
}

.iconos-nosotros {
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.iconos-nosotros a {
  margin-right: 1rem;
  margin-left: 1rem;
}
.iconos-nosotros a img {
  max-height: 6rem;
  transition: all 0.3s 0.1s ease-out;
}
.iconos-nosotros a img:hover {
  max-height: 6.1rem;
}

.iconos-nosotros-footer a {
  margin-top: 2rem;
}
.iconos-nosotros-footer a img {
  max-height: 4rem;
}

.navegacion {
  position: absolute;
  position: fixed;
  right: 0;
  top: 5.5rem;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 15% 100%);
  padding-top: 0;
  display: flex;
  flex-direction: column;
  white-space: nowrap;
  overflow: hidden;
  background-color: rgb(29, 14, 3);
  width: 0;
  height: 25rem;
  text-align: right;
  transition: all 0.5s cubic-bezier(0, 0.88, 0.63, 0.99);
  z-index: 40;
}
.navegacion a {
  position: relative;
  font-size: 2.5rem;
  text-align: center;
  font-weight: bold;
  color: #d6c97c;
  text-shadow: 1px 1px 1px #000000;
  transition: all 0.3s 0.25s cubic-bezier(0, 0.5, 0, 1);
}
.navegacion a:after {
  border-radius: 1rem;
  border-top: 0.8rem solid #ccb42a;
  content: "";
  position: absolute;
  right: 100%;
  left: 0;
  bottom: 0.14rem;
  transition: right 0.25s 0.1s cubic-bezier(0, 0.5, 0, 1);
}
.navegacion a:hover {
  color: #ccb42a;
  font-size: 2.7rem;
}
.navegacion a:hover:after {
  right: 0;
}

.navegacion.is-visible {
  width: 25rem;
}

.hamburguesa {
  background-color: rgb(29, 14, 3);
  opacity: 0;
  position: absolute;
  top: 5%;
  right: 3%;
  overflow-x: hidden;
  clip-path: polygon(9% 0, 100% 0%, 100% 100%, 9% 100%, 0% 50%);
  width: 0%;
  transition: all 0.3s 0.1s cubic-bezier(0, 0.95, 0.59, 1);
  z-index: 45;
}
@media (min-width: 480px) {
  .hamburguesa {
    right: 0;
  }
}
.hamburguesa svg {
  width: 6.5rem;
}
.hamburguesa:hover {
  transform: scale(1.1);
}

.hamburguesa.is-visible {
  opacity: 1;
  position: fixed;
  width: 8rem;
  overflow-x: hidden;
}

