/*AboutMe Section*/
.about-me {
  position: relative;
  text-align: center;
}
.about-me .boton-mas {
  margin-top: 5rem;
  margin-bottom: 10rem;
}

.about-me::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0rem;
  width: 100%;
  background-color: #ccb42a;
  height: 0.6rem;
}

.no-webp .about-me:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -2;
  background-color: #f6efdc;
}

.webp .about-me:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -2;
  background-color: #f6efdc;
}

.titulo-about-me,
.titulo-research,
.titulo-blog,
.titulo-contacto {
  padding-top: 3rem;
  margin-top: 0;
  opacity: 0;
  transform: translateY(-4rem) scale(0.7);
  transition: transform 1s 0.25s cubic-bezier(0, 1, 0.3, 1), opacity 0.3s 0.25s ease-out;
  will-change: transform, opacity;
}

.titulo-about-me.is-visible,
.titulo-research.is-visible,
.titulo-blog.is-visible,
.titulo-contacto.is-visible {
  opacity: 1;
  transform: none;
}

.contenedor-about-me {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  row-gap: 4rem;
}
.contenedor-about-me div {
  border-radius: 1rem;
  border: 0.6rem solid #ccb42a;
  box-shadow: 0.1rem 0.1rem 0.2rem #b4b2ab;
  gap: 10rem;
  background: linear-gradient(to bottom, rgba(245, 233, 167, 0.4), rgba(255, 255, 255, 0.6));
}
@media (min-width: 768px) {
  .contenedor-about-me {
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-about-me:last-of-type {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 0;
  margin-bottom: 5rem;
}

.about-me-photo {
  min-width: 30rem;
  max-width: 50rem;
  position: relative;
}
.about-me-photo::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1rem;
  background-color: #ccb42a;
  opacity: 1;
}
@media (min-width: 768px) {
  .about-me-photo::after {
    opacity: 0;
  }
}

.seccion-about-me {
  margin-right: 1.5rem;
  margin-left: 1.5rem;
  margin-bottom: 1.5rem;
  display: block;
}

.about-me-intro {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .about-me-intro {
    flex-direction: row;
  }
}

.about-me-intro p {
  margin-right: 1.5rem;
  margin-left: 1.5rem;
  margin-bottom: 6rem;
  font-size: 2.5rem;
}
@media (min-width: 1024px) {
  .about-me-intro p {
    font-size: 3.2rem;
  }
}
.about-me-intro p span {
  font-weight: 600;
  font-size: 4rem;
}

.lista {
  margin-right: 1.5rem;
  text-align: left;
  margin-bottom: 5rem;
}
.lista li {
  margin-bottom: 2rem;
  list-style-type: none;
  line-height: 3rem;
  font-size: 2.5rem;
}
.lista li span {
  color: rgb(207, 173, 80);
  font-weight: 600;
  font-size: 2.5rem;
}

