.quote {
  cursor: pointer;
  margin: 2em auto;
  opacity: 0;
  list-style: none;
  padding: 0.5rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(calc(100% + 12em), -50%) scale(0.6);
  transition: transform 1.5s cubic-bezier(0.7, -0.3, 0.2, 1.3), opacity 0.5s 0.6s ease-out;
  width: 30rem;
}
.quote.previous {
  opacity: 0.5;
  transform: translate(-150%, -50%) scale(0.8);
}
.quote.current {
  cursor: default;
  opacity: 1;
  transform: translate(-50%, -50%);
}
.quote.next {
  opacity: 0.5;
  transform: translate(50%, -50%) scale(0.8);
}
.quote.left {
  transform: translate(calc(-150% - 12em), -50%) scale(0.6);
}

.quote-image {
  background-size: cover;
  filter: grayscale(100%);
  box-shadow: 0 0.2rem 0.4rem rgba(0, 0, 0, 0.6);
