  animation-name: sun;
  animation-duration: 60s;
  animation-iteration-count: infinite;
}
.animacion .rig {
  background-image: url(..//img/foothills.webp);
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
  top: 0%;
  width: 100%;
  height: 100%;
}
.animacion .nubes {
  background-image: url(..//img/nubes.webp);
  position: absolute;
  background-repeat: repeat-x;
  width: 100%;
  height: 80px;
  background-size: contain;
  animation: nubes 40s linear infinite;
}
.animacion .marquee-quote {
  position: absolute;
  left: 50%;
  top: 5%;
}
.animacion .marquee-quote p {
  color: rgb(29, 14, 3);
  font-size: 1.9rem;
