@keyframes sun {
  0% {
    left: -70px;
    top: 50%;
  }
  30% {
    left: 50%;
    top: 0%;
    background-color: rgb(199, 154, 6);
    box-shadow: 0 0 10px rgb(133, 103, 5);
  }
  60% {
    left: 70%;
    top: 20%;
    background-color: rgb(199, 61, 6);
    box-shadow: 0 0 10px rgb(202, 100, 5);
  }
  100% {
    left: calc(100% + 70px);
    top: 50%;
    background-color: rgb(199, 199, 199);
    box-shadow: 0 0 10px rgb(255, 255, 255);
  }
}
@keyframes fondo {
  0% {
    background-color: rgb(248, 247, 199);
  }
  30% {
    background-color: rgb(188, 249, 253);
  }
  60% {
    background-color: rgb(236, 140, 14);
  }
  80% {
    background-color: rgb(83, 62, 129);
  }
  100% {
    background-color: rgb(248, 247, 199);
  }
}
@keyframes nubes {
  0% {
    background-position: 0 0;
    opacity: 0;
  }
  25% {
    background-position: 25% 0;
    opacity: 0.6;
  }
  50% {
    background-position: 50% 0;
    opacity: 0.8;
  }
  75% {
    background-position: 75% 0;
    opacity: 0.6;
  }
  100% {
    background-position: 100% 0;
    opacity: 0;
  }
}
